const Menu = [
  {
    title: 'Dashboard',
    icon: 'dashboard',
    name: 'DashboardContent'
  },
  {
    title: 'Data User',
    icon: 'bar_chart',
    name: 'patien'
  },
  {
    title: 'Antrian Pasien',
    icon: 'queue',
    name: 'patientQueue'
  },
  {
    title: 'Saran Dokter',
    icon: 'record_voice_over',
    name: 'saranDokter'
  }
]
// reorder menu
Menu.forEach(item => {
  if (item.items) {
    item.items.sort((x, y) => {
      let textA = x.title.toUpperCase()
      let textB = y.title.toUpperCase()
      return textA < textB ? -1 : textA > textB ? 1 : 0
    })
  }
})

export default Menu



