<template>
  <div>
  <v-toolbar color="indigo" fixed dark app>
    <v-toolbar-title class="ml-0 pl-3"> </v-toolbar-title>
    <v-toolbar-side-icon @click.stop="handleDrawerToggle"></v-toolbar-side-icon>
    <v-spacer></v-spacer>
    <v-btn icon @click="handleFullScreen()">
      <v-icon>fullscreen</v-icon>
    </v-btn>
    <v-menu
      offset-y
      origin="center center"
      :nudge-bottom="10"
      transition="scale-transition"
    >
      <v-btn icon large flat slot="activator" style="margin-right:100px">
        <v-avatar size="30px">
          <img :src="require(`@/assets/m.png`)">
        </v-avatar>
      </v-btn>
      <v-list>
        <v-list-tile 
          v-for="(item, index) in items"
          :to="!item.href ? { name: item.name } : null"
          :href="item.href"
          @click="item.click"
          ripple="ripple"
          :disabled="item.disabled"
          :target="item.target"
          rel="noopener"
          :key="index"
        >
          <v-list-tile-action v-if="item.icon">
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-menu>
  </v-toolbar>

  <v-navigation-drawer
    id="appDrawer"
    :mini-variant.sync="mini"
    fixed
    :dark="$vuetify.dark"
    app
    v-model="drawer"
    width="260"
  >
    <v-toolbar color="indigo darken-1" dark>
      <img :src="require(`@/assets/m.png`)" style="width: 36px;">
      <v-toolbar-title class="ml-0 pl-3">
        <span class="hidden-sm-and-down">Yescare</span>
      </v-toolbar-title>
    </v-toolbar>
    <vue-perfect-scrollbar
      class="drawer-menu--scroll"
      :settings="scrollSettings"
    >
      <v-list dense expand>
        <template v-for="(item, i) in menus">
          <!--group with subitems-->
          <v-list-group
            v-if="item.items"
            :key="item.name"
            :group="item.group"
            :prepend-icon="item.icon"
            no-action="no-action"
          >
            <v-list-tile slot="activator" ripple="ripple">
              <v-list-tile-content>
                <v-list-tile-title>{{ item.title }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <template v-for="(subItem, i) in item.items">
              <!--sub group-->
              <v-list-group
                v-if="subItem.items"
                :key="subItem.name"
                :group="subItem.group"
                sub-group="sub-group"
              >
                <v-list-tile slot="activator" ripple="ripple">
                  <v-list-tile-content>
                    <v-list-tile-title>{{ subItem.title }}</v-list-tile-title>
                  </v-list-tile-content>
                </v-list-tile>
                <v-list-tile
                  v-for="(grand, i) in subItem.children"
                  :key="i"
                  :to="genChildTarget(item, grand)"
                  :href="grand.href"
                  ripple="ripple"
                >
                  <v-list-tile-content>
                    <v-list-tile-title>{{ grand.title }}</v-list-tile-title>
                  </v-list-tile-content>
                </v-list-tile>
              </v-list-group>
              <!--child item-->
              <v-list-tile
                v-else
                :key="i"
                :to="genChildTarget(item, subItem)"
                :href="subItem.href"
                :disabled="subItem.disabled"
                :target="subItem.target"
                ripple="ripple"
              >
                <v-list-tile-content>
                  <v-list-tile-title
                    ><span>{{ subItem.title }}</span></v-list-tile-title
                  >
                </v-list-tile-content>
                <v-list-tile-action v-if="subItem.action">
                  <v-icon :class="[subItem.actionClass || 'success--text']">{{
                    subItem.action
                  }}</v-icon>
                </v-list-tile-action>
              </v-list-tile>
            </template>
          </v-list-group>
          <v-subheader v-else-if="item.header" :key="i">{{
            item.header
          }}</v-subheader>
          <v-divider v-else-if="item.divider" :key="i"></v-divider>
          <!--top-level link-->
          <v-list-tile
            v-else
            :to="!item.href ? { name: item.name } : null"
            :href="item.href"
            ripple="ripple"
            :disabled="item.disabled"
            :target="item.target"
            rel="noopener"
            :key="item.name"
          >
            <v-list-tile-action v-if="item.icon">
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            </v-list-tile-content>
            <v-list-tile-action v-if="item.subAction">
              <v-icon class="success--text">{{ item.subAction }}</v-icon>
            </v-list-tile-action>
          </v-list-tile>
        </template>
      </v-list>
    </vue-perfect-scrollbar>
  </v-navigation-drawer>
  <div class="background-dashboard"></div>
        <div id="appDashboard">
            <main class="dashboardContent" :class="{'showLeft': !drawer}">
            <transition name="fade">
            <router-view></router-view>
            </transition>
            </main>
        </div>
  </div>
</template>

<script>
import menu from '@/api/menu'
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import Util from '@/components/util'
export default {
  name: 'app-drawer',
  name1: 'app-toolbar',
  components: {
    VuePerfectScrollbar
  },
  props: {
    expanded: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      notif: [],
      user: {},
      error: '',
      loading: true,
      roles: localStorage.getItem('roles'),
      name: localStorage.getItem('name'),
      activeFirst: false,
      wrongPassword: false,
      leftNav: false,
      showDropDown: false,
      mini: false,
      drawer: true,
      menus: menu,
      scrollSettings: {
        maxScrollbarLength: 160
      },
      items: [
        {
          icon: 'account_circle',
          href: '#',
          title: 'Profile',
          click: e => {
            console.log(e)
          }
        },
        {
          icon: 'settings',
          href: '#',
          title: 'Settings',
          click: e => {
            console.log(e)
          }
        },
        {
          icon: 'fullscreen_exit',
          href: '/logout',
          title: 'Logout',
          click: e => {
            window.getApp.$emit('APP_LOGOUT')
          }
        }
      ]
    }
  },
  mounted () {
    this.getUser()
  },
  computed: {
    inisial () {
      var initials = ''
      var names = this.name.split(' ')
      for (let n = 0; n < 2; n++) {
        if (n < names.length) {
          initials += names[n].substring(0, 1).toUpperCase()
        }
      }
      return initials
    },
    toolbarColor () {
      return this.$vuetify.options.extra.mainNav
    },
    computeGroupActive () {
      return true
    },
    computeLogo () {
      return '@/assets/m.png'
    },
    sideToolbarColor () {
      return this.$vuetify.options.extra.sideNav
    }
  },
  methods: {
    handleDrawerToggle () {
      this.drawer = !this.drawer
    },
    handleFullScreen () {
      Util.toggleFullScreen()
    },
    genChildTarget (item, subItem) {
      if (subItem.href) return
      if (subItem.component) {
        return {
          name: subItem.component
        }
      }
      return { name: `${item.group}/${subItem.name}` }
    },
    getCount () {
      var uri = '/api/count/notif'
      var config = {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }
      this.$http.get(uri, config).then(response => {
        this.count = response.data
      })
    },
    rightDropDown () {
      if (this.showDropDown === true) {
        this.showDropDown = false
      } else {
        this.showDropDown = true
      }
    },
    hamburger () {
      if (this.leftNav === true) {
        this.leftNav = false
      } else {
        this.leftNav = true
      }
    },
    getUser () {
      if (localStorage.getItem('name') !== null) {
        this.loading = false
        return
      }
      this.$http.get(this.$apiUrl + 'user', {
        headers: {
          Authorization: 'Bearer ' + localStorage.getItem('token')
        }
      }).then(response => {
        this.loading = false
        if (response.data.status === 'Token is Expired' || response.data.userdata === null || response.data[0] === 'user_not_found') {
          this.$router.push({ name: 'Logout' })
        } else {
          this.user = response.data.data
          this.name = response.data.data.name
          localStorage.setItem('name', this.user.name)
          localStorage.setItem('id', this.user.id)
        }
      }).catch(error => {
        console.log(error)
        this.$router.push({ name: 'Logout' })
      })
    }
  }
}
</script>
<style lang='stylus'>
#appDrawer
  overflow: hidden
  .drawer-menu--scroll
    height: calc(100vh - 48px)
    overflow: auto
</style>



